const jmDebounce = {
  inserted: function(el, binding) {
    let timer;
    el.addEventListener("click", () => {
      if (timer) {
        clearTimeout(timer);
      }
      timer = setTimeout(() => {
      //:vue binding ,      binding.value(),,, 
        binding.value();
      }, 500);
    });
  },  
  name: 'jmDebounce'
};

jmDebounce.install = Vue => Vue.directive('jmDebounce', jmDebounce);
export default jmDebounce


