<!--    -->
<template>
    <div :style="marginTop"
         class="yo-scroll"
         :class="{'down':(state===0),'up':(state==1),refresh:(state===2),touch:touching}"
         @touchstart="touchStart($event)"
         @touchmove="touchMove($event)"
         @touchend="touchEnd($event)">
        <section class="inner"
                 :style="{ transform: 'translate3d(0, ' + top + 'px, 0)' }">
            <!-- () -->
            <header class="pull-refresh">
                <slot name="pull-refresh">
                    <span class="down-tip"></span>
                    <span class="up-tip"></span>
                    <span class="refresh-tip"></span>
                </slot>
            </header>
            <!--   -->
            <slot>
            </slot>
            <!-- () -->
            <footer class="load-more" v-show="!dataList.noFlag">
                <slot name="load-more">
                    <span v-show="downFlag === false"></span>
                    <span v-show="downFlag === true"></span>
                </slot>
            </footer>
            <!--  -->
            <div class="nullData"
                 v-show="dataList.noFlag"></div>
        </section>
    </div>
</template>
 
<script>
export default {
    name: 'JmScoll',
    // 
    props: {
        marginTop: {
            default: 'margin-top:40px;'
        },
        // 
        offset: {
            type: Number,
            default: 100
        },
        // 
        enableInfinite: {
            type: Boolean,
            default: true
        },
        // 
        enableRefresh: {
            type: Boolean,
            default: true
        },
        // ''
        dataList: {
            default: false,
            required: false
        },
        // 
        onRefresh: {
            type: Function,
            default: undefined,
            required: false
        },
        // 
        onInfinite: {
            type: Function,
            default: undefined,
            require: false
        }
    },
    data() {
        return {
            top: 0, // 
            state: 0, // : 0 / 1  / 2 
            startX: 0, //  X
            startY: 0, //  Y
            touching: false, // -webkit-overflow-scrolling
            infiniteLoading: false, // 
            downFlag: false //
        }
    },
    methods: {
        // 
        touchStart(e) {
            this.startY = e.targetTouches[0].pageY
            this.startX = e.targetTouches[0].pageX
            this.startScroll = this.$el.scrollTop || 0
            this.touching = true //
            this.dataList.noFlag = false //  ''
            this.$el.querySelector('.load-more').style.display = 'block' // 
        },
        // 
        touchMove(e) {
            if (!this.enableRefresh || this.dataList.noFlag || !this.touching) {
                return
            }
            let diff = e.targetTouches[0].pageY - this.startY - this.startScroll
            if (diff > 0) e.preventDefault()
            this.top =
                Math.pow(diff, 0.8) + (this.state === 2 ? this.offset : 0)
            if (this.state === 2) {
                // 
                return
            }
            if (this.top >= this.offset) {
                this.state = 1
            } else {
                this.state = 0
            }
            let more = this.$el.querySelector('.load-more')
            if (!this.top && this.state === 0) {
                more.style.display = 'block'
            } else {
                more.style.display = 'none'
            }
        },
        // 
        touchEnd(e) {
            if (!this.enableRefresh) {
                return
            }
            this.touching = false
            if (this.state === 2) {
                // 
                this.state = 2
                this.top = this.offset
                return
            }
            if (this.top >= this.offset) {
                // 
                this.refresh()
            } else {
                // 
                this.state = 0
                this.top = 0
            }
            // ----begin
            let endX = e.changedTouches[0].pageX,
                endY = e.changedTouches[0].pageY,
                dy = this.startY - endY,
                dx = endX - this.startX
            //
            if (Math.abs(dx) < 2 && Math.abs(dy) < 2) {
                console.log('')
                return
            }
            //--------end--------
            if (!this.enableInfinite || this.infiniteLoading) {
                return
            }
            let outerHeight = this.$el.clientHeight,
                innerHeight = this.$el.querySelector('.inner').clientHeight,
                scrollTop = this.$el.scrollTop,
                ptrHeight = this.onRefresh
                    ? this.$el.querySelector('.pull-refresh').clientHeight
                    : 0,
                bottom = innerHeight - outerHeight - scrollTop - ptrHeight
            if (bottom <= this.offset && this.state === 0) {
                this.downFlag = true
                this.infinite()
            } else {
                this.$el.querySelector('.load-more').style.display = 'none'
                this.downFlag = false
            }
        },
        // 
        refresh() {
            this.state = 2
            this.top = this.offset
            setTimeout(() => {
                this.onRefresh(this.refreshDone)
            }, 300)
        },
        // 
        refreshDone() {
            this.state = 0
            this.top = 0
        },
        // 
        infinite() {
            this.infiniteLoading = true
            setTimeout(() => {
                this.onInfinite(this.infiniteDone)
            }, 2000)
        },
        // 
        infiniteDone() {
            this.infiniteLoading = false
        }
    }
}
</script>
<style scoped>
.yo-scroll {
    font-size: 16px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    z-index: 100;
    height: auto;
    -webkit-overflow-scrolling: touch;
}

.yo-scroll .inner {
    position: absolute;
    top: -50px;
    width: 100%;
    height: auto;
    transition-duration: 300ms;
}

.yo-scroll .inner .pull-refresh {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 50px;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
}
.yo-scroll .inner .load-more {
    height: 50px;
    line-height: 50px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    display: none;
}
.yo-scroll .inner .nullData {
    font-size: 16px;
    color: #999999;
    height: 50px;
    line-height: 60px;
    text-align: center;
}
.down-tip,
.refresh-tip,
.up-tip {
    display: none;
}
.up-tip:before,
.refresh-tip:before {
    content: '';
    display: inline-block;
    width: 110px;
    height: 40px;
    font-size: 16px;
    background-size: 70% !important;
    position: absolute;
    top: 0;
    left: 20%;
}
/* //   .up-tip:before {
//     background: url(../assets/images/down-logo.png) no-repeat left;
//   }
//   .refresh-tip:before {
//     background: url(../assets/images/refresh-logo.gif) no-repeat left;
//   } */
.yo-scroll.touch .inner {
    transition-duration: 0;
}

.yo-scroll.down .down-tip {
    display: block;
}

.yo-scroll.up .up-tip {
    display: block;
}

.yo-scroll.refresh .refresh-tip {
    display: block;
}
</style>


